import React, { useEffect, useMemo, useState } from 'react';
import { collection, getDocs, limit, orderBy, query } from 'firebase/firestore';
import { db, auth } from '@/firebase';
import { Button } from '@/components/ui/button';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { useToast } from '@/hooks/use-toast';

interface TopupDoc {
  id: string;
  user_id?: string | null;
  phone: string;
  country_code: string;
  operator: string;
  operator_id?: number | null;
  amount_local: number;
  amount_usd?: number | null;
  custom_identifier: string;
  provider_txn_id?: string | null;
  status: 'pending' | 'success' | 'failed' | string;
  response_raw?: any;
  created_at?: any;
  updated_at?: any;
}

const AdminTopupsPage: React.FC = () => {
  const { toast } = useToast();
  const [topups, setTopups] = useState<TopupDoc[]>([]);
  const [loading, setLoading] = useState(false);
  const [filter, setFilter] = useState<'all' | 'pending' | 'success' | 'failed'>('all');
  const [balance, setBalance] = useState<any>(null);

  useEffect(() => {
    const fetchTopups = async () => {
      setLoading(true);
      try {
        const q = query(collection(db, 'topups'), orderBy('created_at', 'desc'), limit(100));
        const snap = await getDocs(q);
        const list: TopupDoc[] = snap.docs.map((d) => ({ id: d.id, ...(d.data() as any) }));
        setTopups(list);
      } catch (err: any) {
        console.error('Failed to fetch topups:', err);
        toast({ title: 'خطأ', description: 'تعذر تحميل العمليات', variant: 'destructive' });
      } finally {
        setLoading(false);
      }
    };
    fetchTopups();
  }, [toast]);

  useEffect(() => {
    const fetchBalance = async () => {
      try {
        const idToken = await auth.currentUser?.getIdToken();
        const resp = await fetch('/api/reloadly/balance', {
          headers: idToken ? { Authorization: `Bearer ${idToken}` } : {},
        });
        const data = await resp.json().catch(() => null);
        setBalance(data?.raw ?? data);
      } catch (e) {
        // ignore for now
      }
    };
    fetchBalance();
  }, []);

  const filtered = useMemo(() => {
    if (filter === 'all') return topups;
    return topups.filter((t) => String(t.status).toLowerCase() === filter);
  }, [topups, filter]);

  const retryTopup = async (doc: TopupDoc) => {
    try {
      const idToken = await auth.currentUser?.getIdToken();
      const resp = await fetch('/api/topup/retry', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          ...(idToken ? { Authorization: `Bearer ${idToken}` } : {}),
        },
        body: JSON.stringify({ id: doc.id }),
      });
      const data = await resp.json().catch(() => ({ success: false, message: 'فشل قراءة استجابة الخادم' }));
      if (resp.ok && data?.success) {
        toast({ title: 'أُعيدت المحاولة', description: 'تم إعادة محاولة العملية بنجاح' });
      } else {
        toast({ title: 'فشل إعادة المحاولة', description: data?.message || 'حدث خطأ أثناء إعادة المحاولة', variant: 'destructive' });
      }
    } catch (err: any) {
      console.error('Retry error:', err);
      toast({ title: 'خطأ في الاتصال', description: 'تعذر الاتصال بالخادم. حاول لاحقاً.', variant: 'destructive' });
    }
  };

  return (
    <div className="p-4">
      <div className="flex items-center justify-between mb-4">
        <h1 className="text-xl font-semibold">لوحة تقارير الشحن</h1>
        <div className="text-sm text-gray-600">
          رصيد Reloadly: {balance?.balance ?? balance?.availableBalance ?? 'غير متاح'}
        </div>
      </div>

      <div className="flex items-center gap-2 mb-4">
        <span>الحالة:</span>
        <Select value={filter} onValueChange={(v: any) => setFilter(v)}>
          <SelectTrigger className="w-40">
            <SelectValue placeholder="اختر الحالة" />
          </SelectTrigger>
          <SelectContent>
            <SelectItem value="all">الكل</SelectItem>
            <SelectItem value="pending">في الانتظار</SelectItem>
            <SelectItem value="success">ناجحة</SelectItem>
            <SelectItem value="failed">فاشلة</SelectItem>
          </SelectContent>
        </Select>
      </div>

      <div className="overflow-x-auto">
        <table className="min-w-full text-sm">
          <thead>
            <tr className="text-right">
              <th className="px-2 py-1">ID</th>
              <th className="px-2 py-1">User</th>
              <th className="px-2 py-1">Phone</th>
              <th className="px-2 py-1">Operator</th>
              <th className="px-2 py-1">Amount</th>
              <th className="px-2 py-1">Status</th>
              <th className="px-2 py-1">ProviderTxn</th>
              <th className="px-2 py-1">Created</th>
              <th className="px-2 py-1">Actions</th>
            </tr>
          </thead>
          <tbody>
            {loading ? (
              <tr><td className="p-4" colSpan={9}>جارٍ التحميل...</td></tr>
            ) : filtered.length === 0 ? (
              <tr><td className="p-4" colSpan={9}>لا يوجد بيانات</td></tr>
            ) : (
              filtered.map((t) => (
                <tr key={t.id} className="border-t">
                  <td className="px-2 py-1 font-mono">{t.id}</td>
                  <td className="px-2 py-1">{t.user_id || '-'}</td>
                  <td className="px-2 py-1">{t.phone}</td>
                  <td className="px-2 py-1">{t.operator}</td>
                  <td className="px-2 py-1">{t.amount_local}</td>
                  <td className="px-2 py-1">{t.status}</td>
                  <td className="px-2 py-1">{t.provider_txn_id || '-'}</td>
                  <td className="px-2 py-1">{t.created_at?.toDate ? t.created_at.toDate().toLocaleString() : '-'}</td>
                  <td className="px-2 py-1">
                    {String(t.status).toLowerCase() === 'failed' && (
                      <Button variant="outline" onClick={() => retryTopup(t)}>Retry</Button>
                    )}
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default AdminTopupsPage;